.header-section{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.header-section button{
  background-color:#e2e2e2;
}
.header-section button:hover{
  background-color:#e2e2e2 !important;
}
.active-tab{
    background-color:#0b7bc1!important;
}

.payment-header{
    background-color: #e2e2e2;
    padding-top: 1rem;
    padding-bottom: 1rem;
}
.payment-header-card{
    background-color: #e2e2e2 !important;
    padding-top: 1rem !important;
    padding-bottom: 1rem !important;
    border: none !important;
    border-radius: 0 !important;
}

.button-section-next-prev{
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.tab{
    display: none;
}

.tab-active-content{
    display: block;
}
.fromCheckbox {display: flex;align-items: center;}
.termsTxt{font-size: 17px;padding-left: 5px;}
.orderConfrimationList li {
    font-size: 18px;
    font-weight: 600;
    color: #363636;
    line-height: 30px;
}

.priceCartPage{
    font-size: 16px;
    font-weight: bolder;
    padding: 0 !important;
    border-top: 0 !important;
}

td.aligneMiddle {
    vertical-align: middle !important;
}
.order-successful {
    justify-content: center;
    display: flex;
}
.terms-and-condition{
    display: flex;
}
.cardTable .btn:hover{
   background-color: #bd2130;
}
